body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 2s linear;
          animation: App-logo-spin infinite 2s linear;
  height: 200px;
  width: 200px;
  -webkit-transform-origin: 100px 100px;
          transform-origin: 100px 100px;
}

.nose {
  fill: #f9c5e7;
}

.leftEye, .rightEye {
  fill: #000000;
  -webkit-animation: Eyes-scale linear 1s alternate;
          animation: Eyes-scale linear 1s alternate;
}

.leftEye {
  -webkit-transform-origin: 225px 275px;
          transform-origin: 225px 275px;
}

.rightEye {
  -webkit-transform-origin: 300px 275px;
          transform-origin: 300px 275px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes Eyes-scale {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  to {
    -webkit-transform: scale(0);
            transform: scale(0);
  }

}

@keyframes Eyes-scale {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  to {
    -webkit-transform: scale(0);
            transform: scale(0);
  }

}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes example {
    0% {color: green}
    100% {
        color: red;
        visibility: hidden
    }
}

@keyframes example {
    0% {color: green}
    100% {
        color: red;
        visibility: hidden
    }
}

.labelOff {
    -webkit-animation: example 1s linear 1s;
            animation: example 1s linear 1s
}
